import Swal from "sweetalert2";
import { myfetch } from "../../helpers/fetch";
import { types } from "../type/types";
import { finishLoading, loading } from "./ui";


export const fetchSaveTodo = (todo) => {
    return async (dispatch, getState) => {
        try {
            const uid = getState().auth.uid;
            todo = { ...todo, user: uid };
            const resp = await myfetch("todo/save", todo, "POST");
            const body = await resp.json();
            if(todo.id){
                dispatch(updateTodo(body));
            } else {
                dispatch(saveTodo(body));
            }
        } catch (error) {
            console.log(error);
            Swal.fire({
                icon: 'error',
                title: 'Oops...',
                text: "Something went wrong"
            });
        }
    }
}

export const fetchDeleteTodo = (id) => {
    return async (dispatch) => {
        try {
            const resp = await myfetch(`todo/delete/${id}`, null, "DELETE");
            const body = await resp.json();
            if (body === true) {
                dispatch(deleteTodo(id));
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: "Something went wrong"
                });
            }
        } catch (error) {
            console.log(error);
        }
    }
}

export const fetchDeleteChecked = () => {
    return async (dispatch, getState) => {
        try {
            const uid = getState().auth.uid;
            const resp = await myfetch("todo/deletechecked/" + uid, null, "DELETE");
            const body = await resp.json();
            if (body === true) {
                dispatch(deleteChecked());
            } else {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: "Something went wrong"
                });
            }
        } catch (error) {
            console.log(error);
        }
    }
}

export const fetchTodoList = () => {
    return async (dispatch, getState) => {
        dispatch(loading());
        try {
            const uid = getState().auth.uid;
            const resp = await myfetch("todo/"+uid, null);
            const body = await resp.json();
            dispatch(setTodoList(body.reverse()));
        } catch (e) {
            console.log(e);
            Swal.fire({
                icon: 'error',
                title: 'Oops...',
                text: "Something went wrong"
            });
        }
        dispatch(finishLoading());
    }
}

export const setTodoList = (todos) => ({
    type: types.setList,
    payload: {
        todos
    }
});

export const selectTodo = (id) => ({
    type: types.selectTodo,
    payload: id
});

export const saveTodo = (todo) => ({
    type: types.saveTodo,
    payload: todo
});

export const updateTodo = (todo) => ({
    type: types.editTodo,
    payload: todo
});

export const deleteTodo = (id) => ({
    type: types.deleteTodo,
    payload: id
});

export const deleteChecked = () => ({
    type: types.deleteChecked
});

export const reset = () => ({
    type: types.reset
});